.form-input__error {
    outline-color: red;
}
.comments-enter {
    opacity: 0;
}

.comments-enter.comments-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.comments-leave {
    opacity: 1;
}

.comments-leave.comments-leave-active {
    opacity: 0;
    transition: opacity 500ms ease-in;
}
.article-enter {
    opacity: 0.01;
}

.article-enter.article-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
}

.article-appear {
    opacity: 0.01;
}

.article-appear.article-appear-active {
    opacity: 1;
    transition: opacity 1000ms ease-in;
}

.article-leave {
    opacity: 1;
}

.article-leave.article-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}
html, body {
  height: 100%; }

body {
  background-color: #fff; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

.page-container {
  padding: 2%; }

.title {
  text-align: center; }

.article__header {
  display: flex;
  align-items: center;
  margin-bottom: 0; }

.article__title {
  margin-right: 1rem;
  font-size: 24px;
  font-weight: bold; }

